<template>
  <div class="home">
    <div id="yiyan">
      <span>{{ obj.output }}</span>
      <span class="typed-cursor">|</span>
    </div>
  </div>
</template>

<script>
import EasyTyper from 'easy-typer-js'
import { onMounted, reactive, onUnmounted, ref } from '@vue/runtime-core'
import { yiyan } from '@/api/api'
import axios from 'axios'
export default {
  name: 'home',
  setup () {
    var obj = new reactive({
      output: '',
      isEnd: false,
      speed: 80,
      singleBack: false,
      sleep: 800,
      type: 'rollback',
      backSpeed: 40,
      sentencePause: false
    })
    var typed = ''
    function fetchData () {
      // yiyan().then(res => console.log(res))
      // axios.get('/yiyan').then(res => console.log(res))
      yiyan().then(res => { return res.data }).then(({ hitokoto, from }) => {
        initTyped(hitokoto + ' — —「' + from + '」')
      })
    };
    function initTyped (input) {
      console.log(input)
      typed = new EasyTyper(obj, input, fetchData)
    };
    onMounted(() => {

      fetchData();
    });
    onUnmounted(() => {
     typed.close()
    })
    return {
      obj
    }
  }
}
</script>
